import React from "react";
import { useStateValue } from "../services/StateProvider";
import Image1 from "./pics/fg.png";
import Image2 from "./pics/fg.png";
import Image3 from "./pics/fg.png";
import Image5 from "./pics/fg.png";
import "flickity/css/flickity.css";

const images = [Image1, Image2, Image3, Image5];

const ProductoOferta = ({ id, titulo, precio, rating, imagen }) => {
  const [{}, dispatch] = useStateValue();

  const aniadirProducto = () => {
    // Añadir un item a la canasta
    dispatch({
      type: "ADD_TO_BASKET",
      item: {
        id: id,
        titulo: titulo,
        imagen: imagen,
        precio: precio,
        rating: rating,
      },
    });
  };
  return (
    <div className="container__oferta">
      <div className="producto__info titulo-oferta">
        <p className="producto__titulo ">{titulo}</p>
        <p className="producto__precio">
          <small>S/.</small>
          <strong>{precio}</strong>
        </p>
      </div>
      <img src={Image1} className="imagen__oferta" alt="" />
      <div onClick={aniadirProducto} className="buttons btn-ofertas">
        <div className="boton__comprar">
          <a
            className="boton effect04"
            data-sm-link-text="CLICK"
            target="_blank"
          >
            <span>COMPRAR</span>
          </a>
        </div>
      </div>
    </div>
  );
};

export default ProductoOferta;
